@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f1f1f1;
}
.nav {
  
  height: 70px;
  padding: 10px;
  box-sizing: border-box;
  position: -webkit-sticky;
  position: sticky;
  top:0;
  display: flex;
  justify-content: center;
  align-items: center;
  
  z-index: 10;
}
.search-container input {
  height: 43px;
  width: 600px;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  border: none;
  font-size: 16px;
  padding: 0 10px;
  box-sizing: border-box;
}
.search-container {
  position: relative;
}
.search-results {
  position: absolute;
  background: #fff;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #e1e1e1;
  top: 42px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  z-index: 100;
}
.search-result .movie-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.search-result .movie-info button {
  margin: 20px 10px;
  background: #21ba45;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
  border: none;
  padding: 9px 0;
  border-radius: 4px;
}
.search-result {
  display: flex;
}
.search-result img {
  height: 150px;
}
.search-result > div span {
  font-size: 20px;
  padding: 10px;
  font-weight: 600;
}

.search-container input:focus,
.search-container button:focus,
.footer button:focus,
.search-result .movie-info button:focus {
  outline: none;
}
.search-container button:hover,
.footer button:hover,
.search-result .movie-info button:hover {
  cursor: pointer;
}
.read-or-hide{
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
}
#search-btn {
  background: #f2711c;
  height: 43px;
  color: #fff;
  border: none;
  font-size: 15px;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  position: relative;
  top: -1px;
}
.main {
  
  padding: 30px 10%;
  box-sizing: border-box;
}
.main .tabs {
  display: flex;
  font-size: 20px;
  margin-bottom: 20px;
}
.main .tabs .tab {
  background: #fff;
  padding: 10px;
  margin-right: 13px;
  border-radius: 3px;
  border: 1px solid #e1e1e1;
}
.active-tabs {
  background: #2185d0 !important;
  color: white;
}
.main .tabs .tab:hover {
  cursor: pointer;
  background: #e1e1e1;
}
.movie-card {
  background: #fff;
  padding: 0;
  border: none;
  border-radius: 3px;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
  display: flex;
}
.movie-card:not(:first-child) {
 margin-top: 20px;
}
.movie-card:last-child {
  margin-bottom: 50px;
}
.movie-card .left {
  flex: 1 1;
}
.movie-card .left img {
  height: 300px;
  box-sizing: border-box;
}
.movie-card .right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 10 1;
  margin:3% 5%;
}
.movie-card .title {
  
  font-size: 25px;
  font-weight: 600;
}
.movie-card .plot {
  margin-top: 20px;
  
}
.footer {
  

  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
  box-sizing: border-box;
}
.footer .rating {
  font-size: 20px;
  font-weight: 600;
}
.footer button {
  font-size: 15px;
  padding: 7px 10px;
  text-shadow: none;
  border-radius: 3px;
  border: none;
  font-weight: 600;
}
.favourite-btn {
  background-color: #2185d0;
  color: #fff;
}
.unfavourite-btn {
  background-color: #db2828;
  color: #fff;
  text-shadow: none;
}
.no-movies {
  padding: 20px 0;
  font-size: 20px;
  color: #6d6d6d;
}
@media only screen and (max-width: 600px) {
 .movie-card{
   display: flex;
   flex-direction: column;
 }
 .left img{
   width: 100%;
   height: 200px;
 }
 .main {
  
  padding: 30px 12%;
  box-sizing: border-box;
}
}

/* Global ------------------------------------------------------ */


  
  .nav {
    margin: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; /*FF fix*/
    height: 100%;
    font-family: Geneva, sans-serif;
    background: hsl(210, 30%, 0%) radial-gradient( hsl(210, 30%, 20%), hsl(210, 30%, 0%));
  }
  
  p {
      margin: 0;
  }
  
  
  /* box ------------------------------------------------------ */
  
  #box {
    text-align: center;
    font-size: 3em;
    font-weight: bold;
    -webkit-backface-visibility: hidden; /* fixes flashing */
  }
  
  
  /* flashlight ------------------------------------------------------ */
  
  #flashlight {
    color: hsla(0,0%,0%,0);
    perspective: 80px;
    outline: none;
  }
  
  
  /* flash ------------------------------------------------------ */
  
  #flash {
    display: inline-block;
    text-shadow: #bbb 0 0 1px, #fff 0 -1px 2px, #fff 0 -3px 2px, rgba(0,0,0,0.8) 0 30px 25px;
    transition: margin-left 0.3s cubic-bezier(0, 1, 0, 1);
  }
      
  #box:hover #flash {
     text-shadow: #111 0 0 1px, rgba(255,255,255,0.1) 0 1px 3px;
    margin-left: 20px;
    transition: margin-left 1s cubic-bezier(0, 0.75, 0, 1);
  }
  
  
  /* light ------------------------------------------------------ */
  
  #light {
    display: inline-block;
    text-shadow: #111 0 0 1px, rgba(255,255,255,0.1) 0 1px 3px;
  }
  
  #box:hover #light {
    text-shadow: #fff 0 0 4px, #fcffbb 0 0 20px;
    transform: rotateY(-60deg);
    transition:         transform 0.3s cubic-bezier(0, 0.75, 0, 1), text-shadow 0.1s ease-out;
  }
#openPage {
	background-color: #111;
}

.openTitle {
	font-family: "Montserrat";
	text-align: center;
	color: #FFF;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100vh;
    max-width: 100vw;
	max-height: 100%;
   
	letter-spacing: 1px;
}

.OpenHead {
	background-image: url(https://media.giphy.com/media/26BROrSHlmyzzHf3i/giphy.gif);
	background-size: cover;
	color: transparent;
    
	-moz-background-clip: text;
	-webkit-background-clip: text;
	text-transform: uppercase;
	font-size: 120px;
	line-height: .75;
	margin: 0;
    max-width: 100%;
}
/* styling my button */

.white-mode {
	text-decoration: none;
	padding: 7px 10px;
    font-size: large;
    font-weight: 900;
	background-color: #122;
	border-radius: 3px;
	color: #FFF;
	transition: .35s ease-in-out;
	position: absolute;
	left: 15px;
	bottom: 15px;
	font-family: "Montserrat";
}

.white-mode:hover {
	background-color: #FFF;
	color: #122;
}
@media only screen and (max-width: 600px) {.OpenHead{
	font-size: 80px;
}}

